<template>
  <Directory v-if="this.state == 'dir'" :title="title" :items="items"></Directory>
  <h1 v-if="this.state == 'file'"> FUCK {{renderedMd}} </h1>
</template>

<script>
import Directory from './Directory.vue'
import axios from 'axios';

export default {
  name: 'notes',
  props: [],
  data () {
    return {
      title: "Notes",
      items: [],
      errors: [],
      state: "dir",
      renderedMd: null
    }
  },
  watch: {
    $route (to, from){
        if (!this.$route.params.file) this.updateItems()
        else this.render()
    }
  },
  created() {
    if (!this.$route.params.file) this.updateItems()
    else this.renderMd()
  },
  methods: {
    updateItems: function() {
      this.state = "dir"
      let url = ""
      if(this.$route.params.course) {
        url = "/notesapi/"+this.$route.params.course
        this.title = this.$route.params.course
      }else{
        url = "/notesapi"
      }
      axios.get(url).then(result => {
        this.items = result.data
      }).catch(err => {
          this.errors.push(err)
      })
    },
    renderMd: function() {
      this.state = "file"
      let url = "/notesapi/"+this.$route.params.course+"/"+this.$route.params.file
      this.renderedMd = null;
      axios.get(url).then(result => {
        this.renderedMd = result.data
      }).catch(err => {
        this.errors.push(err)
      })
    }
  },
  components: {
    Directory
  }
}
</script>

<style>
body {
    background-color: #272B30;
}
</style>



